// Components
import BaseHeader from '@/components/argon-core/BaseHeader'
import MainTitle from '@/components/shack/MainTitle.vue'
import { Table, TableColumn } from 'element-ui'

import { mapState } from 'vuex'

export default {
  layout: 'ShackDash',
  components: {
    BaseHeader,
    MainTitle,
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  async asyncData({ store, $admin, error }) {
    await $admin
      .getAllAdmins()
      .then(data => {
        store.commit('admin/setAdmins', data)
      })
      .catch(err => {
        error({
          statusCode: err.status || 404,
          message: err
        })
      })
  },
  data() {
    return {
      loading: false
    }
  },
  computed: {
    ...mapState({
      admins: state => state.admin.admins.data,
      meta: state => state.admin.admins.meta,
      links: state => state.admin.admins.links
    })
  },
  methods: {
    next() {
      const { next } = this.links

      $admin
        .getAllAdmins(next)
        .then(data => {
          store.commit('admin/setAdmins', data)
        })
        .catch(err => {
          this.$bvToast.toast(err, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    prev() {
      const { prev } = this.links

      $admin
        .getAllAdmins(prev)
        .then(data => {
          store.commit('admin/setAdmins', data)
        })
        .catch(err => {
          this.$bvToast.toast(err, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    },
    removeAdmin(adminId) {
      this.loading = !this.loading

      this.$admin
        .deleteAnAdmin(adminId)
        .then(res => {
          this.$store.commit('admin/removeAdmin', adminId)

          this.$bvToast.toast('Admin removed successfully', {
            title: 'Success',
            variant: 'success',
            solid: true
          })
        })
        .catch(err => {
          this.$bvToast.toast(err, {
            title: 'Error',
            variant: 'danger',
            solid: true
          })
        })
    }
  }
}
